.logo {
    &__text {
        font-family: $text-bree;
        font-size: 2rem;
    }

    &__img {
        margin-right: 5px;
    }
}

.menu {
    margin: 0;

    &__item {
        margin-right: 10px;
    }

    &__link {
        display: block;
        height: 100%;
        // width: 100px;
        padding: 30px 20px 20px 20px;
        font-size: 1.4rem;
        text-decoration: none !important;
        border-bottom: 10px solid transparent;
        transition: all 0.4s ease-in-out;
        color: $main-color !important;

        @include hfa {
            background-color: #12a3d6;
            border-bottom: 10px solid $primary-color;
            color: #c5f4ff !important;
        }
    }
}

.formSearch {
    margin-left: 90px;

    &__field {
        display: flex;
        justify-content: center;
        align-items: center;
        position: relative;
    }

    &__input {
        display: block;
        border: 1px solid $primary;
        padding: 5px;
        font-size: 1.4rem;
        opacity: 0;
        width: 0;
        transition: all 0.6s ease-in-out;

        &.opened {
            opacity: 1;
            width: 150px;
        }
    }

    &__label {
        font-size: 1.2rem;
        margin: 0;
        width: 60px;
        opacity: 1;
        transition: all 0.6s ease-in-out;

        &.hidden {
            width: 0px;
            opacity: 0;
            z-index: -1;
        }
    }

    &__icon {
        height: 36px;
        width: 36px;
        cursor: pointer;
        margin-left: 10px;
    }

    ::placeholder {
        color: $primary;
        font-size: 1.4rem;
    }
}