.footer {
    font-size: 1.2rem;
    color: $gray-color;
    background-color: #2e3e4f;
    padding: 40px 0;

    .footer__title {
        font-family: $text-bebas;
    }
}

.info {
    border-color: $gray-color;
    min-height: 105px;
    max-height: 105px;
    overflow: hidden;

    &--small {
        min-height: 50px;
    }

    &__img {
        height: 60px;
        width: 60px;
        background-color: $white;
        position: relative;
        overflow: hidden;

        img {
            position: absolute;
            left: 50%;
            top: 50%;
            transform: translate(-50%, -50%);
            min-height: 100%;
            min-width: 100%;
        }

        &--small {
            height: 40px;
            display: flex;
            align-items: center;
        }

    }

    &__desc {
        max-height: 60px;
    }
}