.socials {
    padding: 0;
    list-style: none;
}

.social {
    display: inline-block;
    margin: 0 4px;
    margin-bottom: 10px;


    &__link {
        display: inline-block;
        text-decoration: none;
        color: $white;
        width: 42px;
        height: 42px;
        line-height: 42px;
        border-radius: 50%;
        text-align: center;
        font-size: 2.2rem;
        overflow: hidden;
        transition: all 0.4s ease-in-out;

        i {
            transition: all 0.4s ease-in-out;
        }

    }


    &:nth-child(1) &__link {
        background-color: #00acf0;

        i {
            color: $white;
            @include long-shadow(text, #0067b7, 100, false, false, left);
        }
    }

    &:nth-child(2) &__link {
        background-color: #3289da;

        i {
            color: $white;
            @include long-shadow(text, #0f4496, 100, false, false, left);
        }
    }

    &:nth-child(3) &__link {
        background-color: #eb5350;

        i {
            color: $white;
            @include long-shadow(text, #b81c1a, 100, false, false, left);
        }
    }

    &:nth-child(4) &__link {
        background-color: #20b6ff;

        i {
            color: $white;
            @include long-shadow(text, #0a72c5, 100, false, false, left);
        }
    }

    &:nth-child(5) &__link {
        background-color: #0083bd;

        i {
            color: $white;
            @include long-shadow(text, #005494, 100, false, false, left);
        }
    }

    &:nth-child(6) &__link {
        background-color: #d0422b;

        i {
            color: $white;
            @include long-shadow(text, #9a1409, 100, false, false, left);
        }
    }

    //hover
    @include hfa {
        &:nth-child(1) .social__link {
            i {
                @include long-shadow(text, #0067b7, 100, false, false, right);
            }
        }

        &:nth-child(2) .social__link {

            i {
                @include long-shadow(text, #0f4496, 100, false, false, right);
            }
        }

        &:nth-child(3) .social__link {
            i {

                @include long-shadow(text, #b81c1a, 100, false, false, right);
            }
        }

        &:nth-child(4) .social__link {
            i {

                @include long-shadow(text, #0a72c5, 100, false, false, right);
            }
        }

        &:nth-child(5) .social__link {
            i {

                @include long-shadow(text, #005494, 100, false, false, right);
            }
        }

        &:nth-child(6) .social__link {

            i {
                @include long-shadow(text, #9a1409, 100, false, false, right);
            }
        }
    }
}



@font-face {
    font-family: 'fontello';
    src: url('../fonts/fontello.eot');
    src: url('../fonts/fontello.eot') format('embedded-opentype'),
        url('../fonts/fontello.woff2') format('woff2'),
        url('../fonts/fontello.woff') format('woff'),
        url('../fonts/fontello.ttf') format('truetype'),
        url('../fonts/fontello.svg') format('svg');
    font-weight: normal;
    font-style: normal;
}

/* Chrome hack: SVG is rendered more smooth in Windozze. 100% magic, uncomment if you need it. */
/* Note, that will break hinting! In other OS-es font will be not as sharp as it could be */
/*
