@import 'variables';
@import 'mixins';
@import 'bootstrap/bootstrap';
@import 'header';
@import 'footer';
@import 'portfolio';
@import 'about';
@import 'contacts';
@import 'socials';


* {
    box-sizing: border-box;
}

html {
    height: 100%;
    font-size: 10px;
}

body {
    min-height: 100%;
}

.section {
    padding: 35px 0;
    background-color: #f4f4f4;

    h1 {
        font-family: $text-bebas;
    }

    h4 {
        font-size: 1.7rem;
    }

    &--bgCity {
        height: 500px;
        background: #000;
        color: #cccece;
        padding: 25px 0;
        background-image: url(../img/bg.jpg);
        background-repeat: no-repeat;
        background-size: cover;
    }

    &--services {
        position: relative;

        .section__title {
            display: inline-block;
            font-family: $text-bebas;
            font-size: 2.2rem;
            line-height: 0.8;
            padding: 8px 25px;
            color: $white;
            border-radius: 3px;
            position: absolute;
            left: 50%;
            top: 0;
            transform: translate(-50%, -50%);
        }
    }

    &--portfolio {
        background-color: #2e3e4f;
    }


    &--clients {
        padding: 25px 0;
        border-top: 1px solid #b2d5e4;
        border-bottom: 1px solid #b2d5e4;
    }

}

.container {
    &--sm {
        max-width: 860px;
    }
}

.slider {
    min-height: 100%;
}

.service {
    max-width: 160px;

    &--maxw {
        max-width: 230px;
    }

    &__img {
        height: 120px;
        width: 120px;
        border-radius: 50%;
        overflow: hidden;
        position: relative;



        &--sm {
            height: 100px;
            border-radius: 0;
        }

        img {
            position: absolute;
            left: 50%;
            top: 50%;
            transform: translate(-50%, -50%);
        }
    }

    &__title {
        font-size: 1.7rem;
        font-family: $text-bree;
    }

    &__text {
        max-height: 60px;
        overflow: hidden;

        &--lg {
            max-height: 115px;
        }
    }
}

.work {
    width: 280px;

    &--big {
        width: 300px;

        &__imgWrap {
            height: 230px;
        }
    }

    &__imgWrap {
        width: 100%;
        height: 220px;
    }

    &__img {
        height: 100%;
        width: 100%;
        display: flex;
        justify-content: center;
        align-items: center;
        position: relative;

        .img {
            position: absolute;
            left: 50%;
            top: 50%;
            transform: translate(-50%, -50%);
            transition: all 0.4s ease-in-out;
        }
    }

    &__detail {
        width: 100%;
        padding: 20px;
        text-align: center;
        opacity: 0;
        position: absolute;
        left: 50%;
        top: 50%;
        transform: translate(-50%, -50%);
        display: flex !important;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        font-size: 1.7rem;
        transition: all 0.4s ease-in-out;

        h5 {
            font-family: $text-bree;
            font-size: 2rem;
            color: #f0f0f0;
            margin-bottom: 20px;
            max-height: 2rem;
            overflow: hidden;
        }

        p {
            color: #f0f0f0;
            font-size: 1.1rem;
            margin-bottom: 30px;
            max-height: 30px;
            overflow: hidden;
        }
    }

    &__link {
        display: flex;
        padding: 5px 15px;
        border: 1px solid $white !important;
        text-decoration: none !important;
        transition: all 0.4s ease-in-out;

        span {
            margin-left: 10px;
            transition: all 0.4s ease-in-out;
        }

        @include hfa {
            span {
                color: $primary !important;

            }

            border-color: $primary !important;
        }
    }

    @include hfa {
        .work__detail {
            opacity: 1;
        }

        .work__img {
            .img {
                opacity: 0;
            }
        }
    }
}

.offer {
    font-size: 1.2rem;

    &__title {
        font-family: $text-bebas;
        line-height: 1;
    }

    &__text {
        max-width: 700px;
    }

    &__item {
        max-width: 190px;

        p {
            font-size: 1.1rem;
        }
    }

    &__row {
        border-bottom: 1px solid #d9d9d9;
    }
}

.testimonials {
    &__title {
        font-family: $text-bebas;
        font-size: 3.2rem;
    }

    &__quote {
        display: block;
        max-width: 780px;
        font-size: 2.5rem;
        color: #515862;
        font-family: $text-bree;
    }

    &__slider {
        max-width: 220px;
    }

    h5 {
        font-size: 1.4rem;
    }
}

.employee {
    border-bottom: 6px solid #c1c1c1;
    width: 100%;
    padding-bottom: 20px;
    max-height: 116px;
    position: relative;
    cursor: pointer;

    &::after {
        content: '';
        position: absolute;
        left: 50%;
        bottom: -1px;
        transform: translate(-50%, 0);
        border: 10px solid transparent;
        border-bottom: 10px solid $secondary;
        opacity: 0;
        transition: all 0.4s ease-in-out;
    }

    &__img {
        height: 70px;
        border-radius: 50%;
        background-color: #c1c1c1;
        position: relative;
        transition: all 0.4s ease-in-out;
        overflow: hidden;

        .img {
            position: absolute;
            left: 50%;
            top: 50%;
            transform: translate(-50%, -50%);
            min-height: 100%;
            min-width: 100%;
        }
    }

    @include hfa {
        border-color: $secondary;

        .employee__img {
            background-color: $secondary;
        }

        &::after {
            opacity: 1;
        }

    }
}


//helpers

.fz-25 {
    font-size: 2.5rem;
    line-height: 1;
}

.fz-20 {
    font-size: 2rem !important;
}

.fz-13 {
    font-size: 1.3rem !important;
}

.max-w80 {
    max-height: 80px;
    overflow: hidden;
}


@import 'media';