.whtaWeAre {
    &__title {
        font-size: 5rem;
        font-family: $text-bebas;
    }

    &__text {
        font-size: 1.3rem;
    }
}

.belive {

    &__title {
        font-size: 7rem;
        font-family: $text-bebas;
    }

    &__content {
        border-top: 1px solid $gray;
        border-bottom: 3px solid #a5a5a5;
    }
}

.team {
    .team__title {
        font-family: $text-bebas;
        font-size: 5rem;
    }
}

.person {
    padding: 13px;
    background-color: #fff;
    max-width: 380px;
    border: 1px solid $gray;
    box-shadow: -10px -10px 0 0 #d3d3d3;

    &--rightShadow {
        box-shadow: 10px -10px 0 0 #d3d3d3;
    }

    &__header {
        display: flex;
    }

    &__img {
        height: 130px;
        width: 130px;
        position: relative;
        background-color: #b5b5b5;

        img {
            position: absolute;
            left: 50%;
            bottom: 0;
            transform: translate(-50%, 0);
        }
    }

    &__description {
        display: flex;
        flex-direction: column;
        justify-content: flex-start;
        align-items: center;
    }

    &__position {
        font-size: 1.7rem;
        font-family: $text-bree;
    }

    &__name {
        font-family: $text-bebas;
        font-size: 3.3rem;
        line-height: 1;
        margin: 0;
    }

    &__email {
        font-size: 1.3rem;
        color: #94979b;
    }

    &__colors {
        display: flex;
        justify-content: space-around;
        align-items: center;

        .cubeColor {
            height: 25px;
            width: 25px;
            border-radius: 5px;
            margin: 5px;
            border: none;
            outline: none;
            cursor: pointer;

            &:nth-child(1) {
                background-color: #00a1e4;
            }

            &:nth-child(2) {
                background-color: #29aecb;
            }

            &:nth-child(3) {
                background-color: #0481d9;
            }

            &:nth-child(4) {
                background-color: #f7941d;
            }

            @include hfa {
                transform: scale(1.1);
            }
        }
    }
}