.contacts {
    &__title {
        font-family: $text-bebas;
    }

    &__text {
        font-size: 1.3rem;

        span,
        a {
            color: #0988b9;
        }

        a {
            @include hfa {
                color: darken(#0988b9, 10%)
            }
        }
    }

    &__content {
        &>.col-sm-6:first-child {
            border-right: 1px solid #e1e1e1;
            padding-right: 30px;
        }

        &>.col-sm-6:nth-child(2) {
            border-left: 1px solid #ebebeb;
            padding-left: 50px;
        }

    }

    &__imgBg {
        background-image: url(../img/contants/bg.png);
        background-position: center;
        background-repeat: no-repeat;
        background-size: cover;
    }

    &__img {
        padding-bottom: 28px;
        display: flex;
        justify-content: center;
        align-items: center;

        &:last-child {
            padding-bottom: 0;
        }
    }
}

.map {
    height: 500px;

    &__link {
        height: 310px;
        width: 310px;
        border: 3px solid $white;
        cursor: pointer;
        margin-top: 35px;
    }
}

.close {
    font-size: 2.4rem;
}

.follow {
    &__title {
        font-family: $text-bebas;
        font-size: 3.5rem;
    }
}