.clients {
    .client {
        height: 30px;

        &__img {
            height: 30px;
            width: 100%;
            height: 100%;
            position: relative;

            img {
                position: absolute;
                left: 50%;
                top: 50%;
                transform: translate(-50%, -50%);

            }
        }

    }
}

.project {

    &__title {
        font-size: 8rem;
        font-family: $text-bebas;
    }

    &__menu {
        border-top: 1px solid $sky-color;
        border-bottom: 1px solid $sky-color;
        margin-bottom: 30px;
    }
}

.burger {
    height: 100%;

    &__line {
        width: 25px;
        height: 6px;
        border-radius: 5px;
        background-color: #aaaaaa;
        margin-bottom: 3px;
    }
}

.cube {
    display: flex;
    flex-wrap: wrap;
    max-width: 25px;
    margin-top: -4px;

    &__item {
        height: 7px;
        width: 7px;
        background-color: $primary;
        margin: 0.5px;
    }
}