@media screen and (max-width: 1300px) {
    .nav {
        width: 100%;
    }
}

@media screen and (max-width: 992px) {
    .formSearch {
        margin: 0;

        &__field {
            margin: 30px;
        }
    }

    .nav-link {
        padding: 10px 20px;
    }

    //index
    .service {
        &__title {
            min-height: 40px;
        }
    }
}

@media screen and (max-width: 769px) {

    //index
    .service {
        &__title {
            min-height: 20px;
        }
    }

    .nav-link {
        padding: 10px 13px;

        &--mark {
            &::before {
                content: none;
            }
        }

    }

    //about

    .person {
        box-shadow: 0 0 5px 5px #d3d3d3;
    }

    //contacts
    .contacts {
        &__content {
            &>.col-sm-6:first-child {
                padding-right: 15px;
            }

            &>.col-sm-6:nth-child(2) {
                padding-left: 15px;
            }

        }
    }
}

@media screen and (max-width: 576px) {

    //home
    .project {
        &__title {
            font-size: 5rem;
        }
    }

    //about
    .belive {
        &__title {
            font-size: 5rem;
        }
    }

    //contacts
    .contacts {
        &__content {
            &>.col-sm-6:first-child {
                border-right: none;
                padding-right: 15px;
            }

            &>.col-sm-6:nth-child(2) {
                border-left: none;
            }

        }
    }
}

@media screen and (max-width: 576px) {
    h1 {
        font-size: 4rem;
    }
}